export default {
	components:{
		
	},
	data() {
		return {
			text:'选择使用',
			BuyCountNum:'0.00',
			radiocheck:false,
		}
	},
	props:{
		item:{
			type: Object
		},
		types:{
			type: String,
			default: ''
		},
		theme:{
			type: String,
			default: '#ff9000'
		},
		solid:{
			type: String,
			default: '#ff6c00'
		},
		color:{
			type: String,
			default: '#ff9000'
		},
		Buymoeny:{
			type: String,
			default: ''
		},
		checked:{
			type:Boolean,
			default:false
		},
		BuyCountNums:{
			type:Number,
			default:0
		},
		BuyClear:{
			type:Boolean,
			default:false
		}
	},
	mounted(){
		
	},
	watch:{
		checked(){
			console.log("金额是"+this.BuyCountNums)
			console.log("你的全选是"+this.checked)
			if(this.checked){
				if(parseFloat(this.BuyCountNums)>=parseFloat(this.Buymoeny)){
					uni.showToast({
						title: '不可以选择此加油券，优惠券总金额已大于充值金额',
						duration: 1000,
						icon:"none",					
					});
					return;
				}
				if(this.item.state==2){
					return;
				}
				this.item.state=2
				// this.text='已选择'
				this.radiocheck=true
				this.solid="#6495ED"
				var json={
					state:this.item.state,
					id:this.item.ticket,
					money:this.item.price
				}
				this.$emit('completed',json);
			}
			else{
				if(this.item.state==1){
					return;
				}
				this.item.state=1
				// this.text='选择使用'
				this.radiocheck=false
				this.solid="#ff6c00"
				var json={
					state:this.item.state,
					id:this.item.ticket,
					money:this.item.price
				}
				this.$emit('completed',json);
			}
			
		},
		BuyClear(){
			console.log("金额是"+this.Buymoeny)
			// this.text='选择使用'
			this.radiocheck=false
			this.solid="#ff6c00"
		}
	},
	methods: {
		Choice(state){
			console.log("bbbbbb"+this.Buymoeny)
			if(this.Buymoeny==''){
				uni.showToast({
					title: '请输入充值金额',
					duration: 1000,
					icon:"none",					
				});
				return;
			}
			console.log(this.item.price+","+parseFloat(this.Buymoeny))
			if((parseFloat(this.item.price)>=parseFloat(this.Buymoeny))&&this.item.state==1){
				uni.showToast({
					title: '不可以选择此加油券',
					duration: 1000,
					icon:"none",					
				});
				return;
			}
			if(parseInt(state)==1){
				this.item.state=2
				// this.text='已选择'
				this.radiocheck=true
				this.solid="#6495ED"
			}
			else{
				this.item.state=1
				// this.text='选择使用'
				this.radiocheck=false
				this.solid="#ff6c00"
			}
			console.log("金额"+this.Buymoeny)
			console.log(this.solid)
			var json={
				state:this.item.state,
				id:this.item.ticket,
				money:this.item.price
			}
			this.$emit('completed',json);
			
		}
	}
}
